import Carousel from "react-bootstrap/Carousel";
import slides from "assets/slider";
import css from "./Slider.module.css";
import Scroll from "../Scroll";

const Slider = () => {
  return (
    <div style={{ position: "relative", display:"inline-block" }} id="slider">
      <Carousel
        className={css.slider}
        controls={true}
        interval={5000}
        indicators={false}
      >
        {slides.map((s, i) => (
          <Carousel.Item style={{ height: "100%" }} key={i}>
            <img
              className={`${css.slide} d-block w-100`}
              src={s}
              alt="Terre des Arts"
            />
            <Carousel.Caption>
              {i === 0 ?
                <><span>Une élégante résidence,</span><br /><span>réservée à 32 privilégiés</span></> :
                <><span>Votre nouvel art de vivre</span><br /><span>à Villeneuve-Loubet</span></>
              }
            </Carousel.Caption>
          </Carousel.Item>
        ))}
      </Carousel>
      <Scroll to="content" />
    </div>
  );
};

export default Slider;
